* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  line-height: 1.2;
  background: #66524a;
}

#wrapper {
  margin: auto;
}

/* #background-menu {
  display: flex;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-image: url("../images/menu-bg2.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
} */

#menu-div {
  width: min(90%, 60rem);
  position: relative;
  background: rgba(255, 255, 255, 0.59);
  padding: 1rem;
  margin: 2rem auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
  border-radius: 10px 0px 10px 0px;
  /* border: 5px solid rgb(255, 255, 255); */
}

.single-dish {
  margin: 1rem;
  width: 90%;
  display: flex;
  padding: 1.5rem;
  border-radius: 8px 0px 8px 0px;
  flex-direction: column;
  background-color: rgb(243, 239, 239);
  border: solid ;
  .whole-dish{
    display: flex;
  }

  button{
    width: 48%;
    padding: 1.2rem;
    color: white;
    border: none;
    background: #ce2525;
    margin: 1rem auto;
  }

  button:hover{
    background: #c02020;
  }

  button:active{
    transform: scale(0.9);
    transition: 0.5s;
  }

  .dish-image{
    width: 50%;
    border: 2px solid white;
    border-radius: 8px 0px 8px 0px;
  }

  .dish-info{
    text-align: center ;
    display: block;
    margin: auto;

    .dish-price{
      margin-top: 1rem;
      background: goldenrod;
      width: 50%;
      padding: 0.6rem;
      margin: 1rem auto;

    }
  }
}
